const FuncionalidadesInside = function (){

	const init = function(){
		'use strict';
		responsiveJsNoEquire();
		generales();
		slideToggleLineas();
		tabsInfoCovid();
		slidersControls();
		initSliderINS();
		autoSizeINSPrecaucion();
		// transitionRedirect();
	};

	function reportWindowSize() {
		setTimeout(function () {
			responsiveJsNoEquire();
		}, 500);
		autoSizeVideos();
		initSliderINS();
		autoSizeINSPrecaucion();
	}
	window.addEventListener('resize', reportWindowSize);
	
	const responsiveJsNoEquire = function () {
		let windowWidth = window.innerWidth;

		//	DESKTOP
		if (windowWidth >= 1151) {
			let listMitos = document.querySelectorAll('.listMitoRealidad li');
			if (listMitos) {
				if (listMitos.length % 2) {
					listMitos[listMitos.length - 1].style.width = '100%';
					listMitos[listMitos.length - 1].querySelector('.innerMitoRealidad').style.backgroundColor = '#fafafa';
				}
			}
		}
		//	TABLET LANDSCAPE
		else if (windowWidth >= 993 && windowWidth <= 1150) {
			let listMitos = document.querySelectorAll('.listMitoRealidad li');
			if (listMitos) {
				if (listMitos.length % 2) {
					listMitos[listMitos.length - 1].style.width = '100%';
					listMitos[listMitos.length - 1].querySelector('.innerMitoRealidad').style.backgroundColor = '#fafafa';
				}
			}

			if (document.querySelector('.blackboxIframeCompability'))
				document.querySelector('.blackboxIframeCompability').style.display = 'none';
		}
		//	TABLET PORTRAIT 
		else if (windowWidth >= 768 && windowWidth <= 992) {

			if(document.querySelector('.blackboxIframeCompability'))
				document.querySelector('.blackboxIframeCompability').style.display = 'block';
		}
		//	MOBILE
		else if (windowWidth <= 767) {
			if (document.querySelector('.blackboxIframeCompability'))
				document.querySelector('.blackboxIframeCompability').style.display = 'block';
		}
	}

	const generales = function(){

		let btnMedidas_Preguntas = document.querySelectorAll('.listMedida-Pregunta .btnSlideTog');
		if (btnMedidas_Preguntas) {
			FuncionalidadesGenerales.clickSlideToggle(btnMedidas_Preguntas);
		}

	};

	//--------------------- FUNCTIONALITY DROPDOWNS LINEAS ATENCION ---------------------//
	const slideToggleLineas = function () {

		let innerContLineas = document.querySelector('.innerContainerLineasAtencion');

		if (innerContLineas) {
			let btnLineas = innerContLineas.querySelectorAll('.btnSlideTog');
			btnLineas.forEach(linkToggle => {
				
				linkToggle.addEventListener('click', function(){
					this.classList.toggle('active');
					FuncionalidadesGenerales.slideToggle(this.nextElementSibling, 300);
	
					btnLineas.forEach(linkToggle => {
						if (linkToggle !== this) {
							linkToggle.classList.remove('active');
							FuncionalidadesGenerales.slideUp(linkToggle.nextElementSibling, 300);
						}
						// if (this.classList.contains('active')) {
							
						// }
						// else {
						// 	FuncionalidadesGenerales.slideUp(this.nextElementSibling, 300);
						// }
					});
				});
	
			});
		}

	}

	//--------------------- TABS FUNCTIONALITY ---------------------//
	const tabsInfoCovid = function () {

		// ================ ITEM COLLECTED ===================== //
		let navContainer = document.querySelector('.contentMenuDocumentales');
		if (navContainer) {
			let itemNavigation = navContainer.querySelectorAll('li');
	
			// NAVIGATION AMOUNG INFO
			FuncionalidadesGenerales.tabsFunctionality(itemNavigation)

		}
		
		// ================ ESTADISTICA COVID-19 ===================== //
		let menuEstadisticaCovid = document.querySelector('.menuEstadisticaCovid');
		if (menuEstadisticaCovid) {
			
			let mainContainer = document.getElementById('mainContainer');
			let bodyClass = mainContainer.getAttribute('dataClass');
			let itemNavigationMenuEstadistica = menuEstadisticaCovid.querySelectorAll('li');
			
			for (let i = 0; i < itemNavigationMenuEstadistica.length; i++) {
				const listItemNav = itemNavigationMenuEstadistica[i];
				let attrItemClass = listItemNav.querySelector('a').getAttribute('dataBodyClass')

				if (bodyClass == attrItemClass) {
					listItemNav.classList.add('active')
				}
			}
			
		}

	};

	//--------------------- SLIDER FUNCIONALIDAD ---------------------//
	const slidersControls = function () {

		// ALOCUCIONES
		let contentSliderVideos = document.querySelectorAll('.containerListVideos');
		let blackboxVideos = document.querySelector('.blackboxVideos');
		if (contentSliderVideos) {
			for (let i = 0; i < contentSliderVideos.length; i++) {
				const container = contentSliderVideos[i];
				let slideContent = container.querySelector('.slideAlocuciones');
				let imageVideo = container.querySelectorAll('figure');
				let itemEqualHeight = container.querySelectorAll('figcaption');
				
				
				imageVideo.forEach(videoToShow => {
					videoToShow.addEventListener('click', function () {
						let videoCode = this.getAttribute('dataIDVideo');
						// let urlVideo = 'https://www.youtube.com/embed/' + videoCode;
						let iframe = '<iframe src="https://www.youtube.com/embed/' + videoCode +'" frameborder="0">'
						blackboxVideos.querySelector('.innerPopupModal .contentIframe').innerHTML = iframe;
						blackboxVideos.style.display = 'flex';
					});
				});

				blackboxVideos.querySelector('.cerrar').addEventListener('click', function () {
					// this.parentNode.parentNode.style.display = 'none';
					let popupModal = this.parentNode;
					popupModal.querySelector('.contentIframe').innerHTML = '';
				})
				window.addEventListener('click', function (e) {
					if (e.target == blackboxVideos) {
						blackboxVideos.querySelector('.contentIframe').innerHTML = '';
					}
				})
	
				if (slideContent.querySelectorAll('li').length <= 3) {
					window.addEventListener('resize', checkNavigations);
					function checkNavigations(){
						if (window.innerWidth <= 768 && slideContent.querySelectorAll('li').length > 1) {
							container.querySelector('.navigationSlide').removeAttribute('style');
						} else {
							container.querySelector('.navigationSlide').style.display = 'none';
						}	
					};
					checkNavigations();
	
					let sliderAlocuciones = new Swiper(slideContent, {
						// spaceBetween: 10,
						// loop: true,
						// loopFillGroupWithBlank: true,
						// autoplay: {
						// 	delay: 4000,
						// 	disableOnInteraction: true,
						// },
						breakpoints: {
							320: {
								slidesPerView: 1,
								pagination: {
									el: container.querySelector('.navigationSlide .swiper-pagination'),
									clickable: true,
									dynamicBullets: true,
								},
								navigation: {
									nextEl: container.querySelector('.navigationSlide .swiper-button-next'),
									prevEl: container.querySelector('.navigationSlide .swiper-button-prev'),
								},
							},
							768: {
								slidesPerView: 2,
								pagination: {
									el: container.querySelector('.navigationSlide .swiper-pagination'),
									clickable: true,
									dynamicBullets: true,
								},
								navigation: {
									nextEl: container.querySelector('.navigationSlide .swiper-button-next'),
									prevEl: container.querySelector('.navigationSlide .swiper-button-prev'),
								},
								// spaceBetween: 20,
							},
							1024: {
								slidesPerView: 3,
								// spaceBetween: 30,
							},
							1260: {
								slidesPerView: 3,
								// spaceBetween: 30,
							},
						}
					});
				}
				else if (slideContent.querySelectorAll('li').length > 3 && slideContent.querySelectorAll('li').length < 6) {
					let sliderAlocuciones = new Swiper(slideContent, {
						slidesPerView: 1,
						// spaceBetween: 10,
						// loop: true,
						// loopFillGroupWithBlank: true,
						// autoplay: {
						// 	delay: 4000,
						// 	disableOnInteraction: true,
						// },
						pagination: {
							el: container.querySelector('.navigationSlide .swiper-pagination'),
							clickable: true,
							dynamicBullets: true,
						},
						navigation: {
							nextEl: container.querySelector('.navigationSlide .swiper-button-next'),
							prevEl: container.querySelector('.navigationSlide .swiper-button-prev'),
						},
						breakpoints: {
							768: {
								slidesPerView: 2,
								// spaceBetween: 20,
							},
							1024: {
								slidesPerView: 3,
								// spaceBetween: 30,
							},
							1260: {
								slidesPerView: 3,
								// spaceBetween: 30,
							},
						}
					});
				}
				else if (slideContent.querySelectorAll('li').length >= 6){
					let sliderAlocuciones = new Swiper(slideContent, {
						slidesPerView: 1,
						slidesPerColumn: 2,
						// spaceBetween: 10,
						// loop: true,
						// loopFillGroupWithBlank: true,
						// autoplay: {
						// 	delay: 4000,
						// 	disableOnInteraction: true,
						// },
						pagination: {
							el: container.querySelector('.navigationSlide .swiper-pagination'),
							clickable: true,
							dynamicBullets: true,
						},
						navigation: {
							nextEl: container.querySelector('.navigationSlide .swiper-button-next'),
							prevEl: container.querySelector('.navigationSlide .swiper-button-prev'),
						},
						breakpoints: {
							768: {
								slidesPerView: 2,
								slidesPerColumn: 2,
								// spaceBetween: 20,
							},
							1024: {
								slidesPerView: 3,
								slidesPerColumn: 2,
								// spaceBetween: 30,
							},
							1260: {
								slidesPerView: 3,
								slidesPerColumn: 2,
								// spaceBetween: 30,
							},
						}
					});
				}
			}
			autoSizeVideos();
		}

	};

	const autoSizeVideos = function (){
		let contentSliderVideos = document.querySelectorAll('.containerListVideos');
		if (contentSliderVideos) {
			for (let i = 0; i < contentSliderVideos.length; i++) {
				const container = contentSliderVideos[i];
				let itemEqualHeight = container.querySelectorAll('figcaption');
				setTimeout(() => {
					FuncionalidadesGenerales.igualarAlturasVideos(itemEqualHeight);
				}, 500);
			}
		}
	}

	let sliderINSPrecaucion = undefined;
	const initSliderINS = function () {
		let screenWidth = window.innerWidth
		let contentSliderINSPrecaucion = document.querySelector('.containerINSPrecaucion');
		if (screenWidth <= 1439 && contentSliderINSPrecaucion && sliderINSPrecaucion == undefined) {
			let slideContent = contentSliderINSPrecaucion.querySelector('.sliderINSPrecaucion');
			sliderINSPrecaucion = new Swiper(slideContent, {
				slidesPerView: 1,
				// spaceBetween: 10,
				// loop: true,
				// loopFillGroupWithBlank: true,
				// autoplay: {
				// 	delay: 4000,
				// 	disableOnInteraction: true,
				// },
				pagination: {
					el: contentSliderINSPrecaucion.querySelector('.navigationSlide .swiper-pagination'),
					clickable: true,
					dynamicBullets: true,
				},
				navigation: {
					nextEl: contentSliderINSPrecaucion.querySelector('.navigationSlide .swiper-button-next'),
					prevEl: contentSliderINSPrecaucion.querySelector('.navigationSlide .swiper-button-prev'),
				},
				breakpoints: {
					768: {
						slidesPerView: 2,
						// spaceBetween: 20,
					},
					1024: {
						slidesPerView: 3,
						// spaceBetween: 30,
					},
					1260: {
						slidesPerView: 4,
						// spaceBetween: 30,
					},
				}
			});
		} else if (screenWidth > 1439 && contentSliderINSPrecaucion && sliderINSPrecaucion != undefined) {
			sliderINSPrecaucion.destroy();
			sliderINSPrecaucion = undefined;
			contentSliderINSPrecaucion.querySelector('.swiper-wrapper').removeAttribute('style');
			contentSliderINSPrecaucion.querySelector('.swiper-slide').removeAttribute('style');
		}
		
	}

	const autoSizeINSPrecaucion = function (){
		let listaPrecaucionINS = document.querySelector('.listaPrecaucionINS');
		if (listaPrecaucionINS) {
			const itemPrecaucion = listaPrecaucionINS.querySelectorAll('.textContent');
			setTimeout(() => {
				FuncionalidadesGenerales.igualarAlturasVideos(itemPrecaucion);
			}, 500);
		}
	}

	//--------------------- TABS FUNCTIONALITY ---------------------//
	const transitionRedirect = function(){

		let toRedirect = {
			'localUrls': [
				'acciones-06-abril.html',
				'acciones-04-abril.html',
				'acciones-02-abril.html',
				'acciones-01-abril.html',
				'acciones-31-marzo.html',
				'acciones-30-marzo.html',
				'acciones-27-marzo.html',
				'acciones-25-marzo.html',
				'acciones-23-marzo.html',
				'acciones-22-marzo.html',
				'acciones-21-marzo.html',
				'acciones-20-marzo.html',
				'acciones-19-marzo.html',
				'acciones-18-marzo.html',
				'acciones-17-marzo.html',
				'acciones-16-marzo.html',
				'acciones-15-marzo.html',
				'acciones-14-marzo.html',
				'acciones-13-marzo.html',
				'acciones-12-marzo.html',
				'acciones-11-marzo.html',
				'acciones-9-marzo.html'
			]
		};
		
		let currentArchive = window.location.pathname.split('/');
		let index = currentArchive.indexOf('');
		if (index > -1) {
			currentArchive.splice(index, 1);
		}

		for (let i = 0; i < toRedirect.localUrls.length; i++) {
			const currentUrl = toRedirect.localUrls[i];
			if (currentUrl == currentArchive[1]) {
				location.replace('/' + currentArchive[0] + '/acciones/' + currentUrl);
			}
		}
		/*
		FuncionalidadesGenerales.loadJSON(function (response) {
			// Parse JSON string into object
			var listURL = JSON.parse(response);
			
			for (let i = 0; i < listURL.localUrls.length; i++) {
				const currentUrl = listURL.localUrls[i];
				if (currentUrl == currentArchive[1]) {
					location.replace('/' + currentArchive[0] + '/acciones/' + currentUrl);
				}
			}
		});
		*/

	};


	return{
		init:init
	}
}();

document.addEventListener("DOMContentLoaded", function(event) {
	FuncionalidadesInside.init();
});
